.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.skill-section {
    display: flex;
    justify-content: space-around;
    margin: 12px 0px;
}
.skills-title-container{
    font-size: 24px;
    font-weight: 600;
}

.skills-list-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    flex-direction: row;
    top: 20px;
}

@media only screen and (max-width: 720px) {
    .skill-list{
        max-width: 100%;
        justify-content: center;
         
    }
    .skills-section {
        flex-direction: column;
        align-items: center;
        padding: 10px;
        margin-left: auto;
        margin-right: auto;
    }
}