.mobile {
    background: rgba( 142, 45, 226, 0.35 );
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid #4A00E0;
    height: 70vh;
    width: 50vw;
    right: 0px;
    position: absolute;
    padding: 18px 12px;
    top:5px;
    display: flex;
    flex-direction: column;
    z-index: 2;
}

.close-icon {
    display: flex;
    justify-content: flex-end;
    font-size: 20px;
}

.mobile-options {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 24px;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.mobile-option {
    font-size: 24px;
    margin: 12px 0px;
    display: flex;
}

.mobile-option a {
    text-decoration: none;
    color: #C6426E;
    display: flex;

}